.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}

.modal-main {
    position:fixed;
    background: #0a1612d9;
    color: white;
    width: 350px;
    height: auto;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    padding: 15px 20px;
    text-align: left;
    border-radius: 5px;
}

#close-popup-btn {
    font-size: 16px;
